
import { createRouter, createWebHistory } from 'vue-router'
import { auth } from '@/firebase'


const router = createRouter({
  history: createWebHistory(),
  routes :[
    {
      path: '/',
      name: 'Home',
      component: ()=>import( /* webpackChunkName: "home" */ '../view/HomeView.vue'),
      meta: { requiresAuth: true }
    },
    {
      path: '/login',
      name: 'Login',
      component: ()=>import( /* webpackChunkName: "home" */ '../view/LoginView.vue'),
      
      
      
    },
  
    {
      path: '/message',
      name: 'message',
      component: ()=>import( /* webpackChunkName: "home" */ '../view/MessageView.vue'),
      meta: { requiresAuth: true }
      
    },
    
  ]
})
router.beforeEach((to,from,next) =>{
  if (to.path ==='/login' && auth.currentUser) {
  
    console.log('to.path',to.path,auth.currentUser);
    next('/')
    return;
  }
  

  if (to.path ==='/login' && to.matched.some(record => record.meta.requiresAuth) && !auth.currentUser) {
    console.log('to.matched',to.matched,auth.currentUser);
    next('/login')
    return;
  }

  next();
})



export default router
